.header {
  background-color: rgb(0, 130, 210);
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.3rem;
  margin: 0;
}

.header-title {
  margin: 0;
  /* override default h1 margin */
  margin-left: 0.5rem;
  font-size: 20px;
}

.header-right {
  display: flex;
  align-items: center;
}

.username-text {
  margin-right: 1rem;
  font-size: 20px;
}

.logout-button,
.login-button {
  background: transparent;
  border: 1px solid white;
  color: white;
  cursor: pointer;
  margin-right: 0.5rem;
  margin-top: 0.1rem;
}

.logout-button:hover,
.login-button:hover {
  background-color: rgba(255, 255, 255, 0.2);
}